import React from "react";
import { OrbitLayer } from './orbit-layer';
import { Box } from './box'
import { Threat } from './threat'

export default class DynamicLayer extends React.Component {

    constructor() {
        super();
        this.state = {
            civs: [],
            enemies: [],
            flightPath: [],
        }
    }

    componentDidUpdate(prevProps) {
        if (prevProps.markers !== this.props.markers) {
            var turnpoints = this.props.markers.filter(m => m.isMarkerTurnpoint).sort((a, b) => (a?.turnpointIndex ?? 0) - (b?.turnpointIndex ?? 0));
            var flightPath = this.getUpdatedFlight(turnpoints);
            var civs = this.props.markers.filter(m => m.type === "friendly");
            var enemies = this.props.markers.filter(m => m.type === "enemy");
            this.setState({civs: civs, enemies: enemies, flightPath: flightPath})
        }
    }

    getUpdatedFlight(markers) {
        return markers.map((m) => m.relativePosition);
    }

    render() {
        return <div>
            {(this.props.isOrbitActive && this.state.flightPath.length > 1)
                ? <OrbitLayer showLabels={this.props.areLabelsActive} index={this.props.loopIndex} locationList={this.state.flightPath} />
                : undefined}
            {this.state.civs.map((marker, i) => (
                <Box parent='#dynamic-layer' color='blue' x={marker.relativePosition.x} y={marker.relativePosition.y} width={.25} height={.50} rotation={30} />)
            )}
            {this.state.enemies.map((marker, i) => (
                <Threat parent='#dynamic-layer' iconColor='red' circleColor='red' radius={1.5} z={0.05} x={marker.relativePosition.x} y={marker.relativePosition.y} cZ={.6} />)
            )}
        </div>;
    }
}
