import React from "react";
import {Borders} from './borders'
import {Text} from './text'

export default class BordersLayer extends React.Component {
    render() {
        return (
            <div>
                <Borders parent='#borders-layer' />
                {this.props.showLabels
                    ? <Text parent='#borders-layer' color='yellow' x={3.75} y={3.5} z={0} text='Country A' />
                    : undefined}
                {this.props.showLabels
                    ? <Text parent='#borders-layer' color='yellow' x={5.1} y={3.5} z={0} text='Country B' />
                    : undefined}
            </div>
        );
    }
}