import React from "react";
import { Model } from "./models";

export class OrbitLayer extends React.Component {
    constructor() {
        super();
        this.begin = false;
        this.arrayIndex = 0;
        this.update = true;
        this.positionIndex = 0;
    }

    componentDidUpdate(prevProps) {
        if (prevProps.index !== this.props.index && this.begin) {
            this.positionIndex++;
        }
        if (prevProps?.locationList?.length !== this.props.locationList?.length) {
            this.arrayIndex = 0;
            this.begin = false;
            this.numSteps = 1;
            this.positionIndex = 0;
        }
        if (this.props.locationList && this.props.locationList.length > 1) {
            this.locationList = this.props.locationList;
            var finalPositionIndex = (this.arrayIndex + 1) % this.locationList.length;
            var distance = Math.sqrt(Math.pow((this.locationList[finalPositionIndex].x - this.locationList[this.arrayIndex].x), 2) +
                Math.pow((this.locationList[finalPositionIndex].y - this.locationList[this.arrayIndex].y), 2));
            this.numSteps = parseInt(distance / 2 * 30);
            this.originX = this.locationList[this.arrayIndex].x;
            this.originY = this.locationList[this.arrayIndex].y;
            this.finalX = this.locationList[finalPositionIndex].x;
            this.finalY = this.locationList[finalPositionIndex].y;
            this.stepX = (this.finalX - this.originX) / this.numSteps;
            this.stepY = (this.finalY - this.originY) / this.numSteps;

            this.originWX = this.locationList[this.arrayIndex].x - .5;
            this.originWY = this.locationList[this.arrayIndex].y + .7;
            this.finalWX = this.locationList[finalPositionIndex].x - .5;
            this.finalWY = this.locationList[finalPositionIndex].y + .7;
            this.stepWX = (this.finalWX - this.originWX) / this.numSteps;
            this.stepWY = (this.finalWY - this.originWY) / this.numSteps;
            this.begin = true;
        }
    }

    calculateEndpoint(origin, step) {
        if (this.begin) {
            if (!(this.positionIndex % this.numSteps)) {
                if (this.update) {
                    this.arrayIndex = (this.arrayIndex + 1) % this.locationList.length;
                    this.update = false;
                    var finalPositionIndex = (this.arrayIndex + 1) % this.locationList.length;
                    var distance = Math.sqrt((this.locationList[finalPositionIndex].x - this.locationList[this.arrayIndex].x) ^ 2 +
                        (this.locationList[finalPositionIndex].y - this.locationList[this.arrayIndex].y) ^ 2);
                    this.positionIndex = 0;
                    this.numSteps = parseInt(distance / 2 * 30);
                    this.originX = this.locationList[this.arrayIndex].x;
                    this.originY = this.locationList[this.arrayIndex].y;
                    this.finalX = this.locationList[finalPositionIndex].x;
                    this.finalY = this.locationList[finalPositionIndex].y;
                    this.stepX = (this.finalX - this.originX) / this.numSteps;
                    this.stepY = (this.finalY - this.originY) / this.numSteps;

                    this.originWX = this.locationList[this.arrayIndex].x - .5;
                    this.originWY = this.locationList[this.arrayIndex].y + .7;
                    this.finalWX = this.locationList[finalPositionIndex].x - .5;
                    this.finalWY = this.locationList[finalPositionIndex].y + .7;
                    this.stepWX = (this.finalWX - this.originWX) / this.numSteps;
                    this.stepWY = (this.finalWY - this.originWY) / this.numSteps;
                }
                return this.locationList[this.arrayIndex];
            }
            else {
                this.update = true;
                return origin + ((this.positionIndex % this.numSteps) * step);
            }
        }
    }

    calculateAngle(point1, point2) {
        if (point1 && point2) {
            return Math.atan2(-point2.y + point1.y, point2.x - point1.x) * 180 / Math.PI + 90;
        }
        return 0;
    }

    render() {
        if (this.begin) {
            return (
                <div>
                    <Model parent='#orbit-layer'
                        x={this.calculateEndpoint(this.originX, this.stepX)}
                        z={this.calculateEndpoint(this.originY, this.stepY)}
                        y={.1}
                        angle={this.calculateAngle({ x: this.originX, y: this.originY }, { x: this.finalX, y: this.finalY })} />
                    <Model parent='#orbit-layer'
                        x={this.calculateEndpoint(this.originWX, this.stepWX)}
                        z={this.calculateEndpoint(this.originWY, this.stepWY)}
                        y={.1}
                        angle={this.calculateAngle({ x: this.originX, y: this.originY }, { x: this.finalX, y: this.finalY })} />
                </div>
            );
        }
        else return (<div></div>);
    }
}