import React from "react";
import { WeatherBlob } from "./weatherBlob";

export default class WeatherLayer extends React.Component {
    constructor() {
        super();
        this.originX = -1;
        this.originY = -1;
        this.finalX = 4;
        this.finalY = 4;
        this.stepX = (this.finalX - this.originX) / 100.0;
        this.stepY = (this.finalY - this.originY) / 100.0;
        this.stepRotation = .1;
    }

    render() {
        return (
            <div>
                <WeatherBlob
                    parent='#weather-layer'
                    x={this.originX + ((this.props.index%100)/2 * this.stepX)}
                    z={this.originY + ((this.props.index%100)/2 * this.stepY)}
                    width={8 - ((this.props.index%100)/2 * .06)}
                    height={8 - ((this.props.index%100)/2 * .04)}
                    rotation={(this.props.index%100)/2 * .5}/>
            </div>
        );
    }
}