import React from 'react';
import ToggleButton from '@material-ui/lab/ToggleButton';
import Button from '@material-ui/core/Button'
import WeatherLayer from './weather-layer';
import WindLayer from './wind-layer';
import BordersLayer from './borders-layer';
import { Popup } from 'reactjs-popup';
import './index.css';
import { Terrain } from './terrain';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import TextField from '@material-ui/core/TextField';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Grid from '@material-ui/core/Grid';
import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import { Box } from './box';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import DynamicLayer from './dynamic-layer';
import Switch from '@material-ui/core/Switch';

export default class WhiteboardMenu extends React.Component {


    constructor() {
        super();
        this.state = {
            loopIndex: 0,
            areZonesActive: true,
            areThreatsActive: true,
            areLabelsActive: true,
            isOrbitActive: true,
            isWeatherActive: false,
            isBordersActive: true,
            isWindActive: false,
            isTerrainActive: false,
            screenLocation: { left: 0, top: 0 },
            topLeft: false,
            bottomLeft: false,
            topRight: false,
            bottomRight: false,
            timer: null,
            markerPropertiesSelectedIndex: -1,
            isMarkerPropertiesDialogOpen: false,
            selectedMarkerType: "none",
            markerPropertiesName: "",
            isMarkerTurnpoint: false,
            markerPropertiesTurnPointNumber: 0,
            markersHeader: "No Assests Found",
            markers: [],
        }
    }

    addMarker(element, label) {
        console.log("marker add: " + element)
        var index = this.state.markers.findIndex((m) => m.element === element)

        var markers = [...this.state.markers];

        var anchorElement = document.querySelector("#anchor");
        var anchorPosition = anchorElement?.getAttribute("position");

        var markerElement = document.querySelector("#" + element);
        var markerPosition = markerElement?.getAttribute("position");

        var relativePosition = { x: markerPosition.x - anchorPosition.x, y: anchorPosition.y - markerPosition.y, z: 0 }

        if (index !== -1) {
            var marker = { ...markers[index] }
            // update position
            marker.position = markerPosition;
            marker.relativePosition = relativePosition;
            marker.isVisible = true;
            markers[index] = marker;
        }
        else {
            markers.push(
                {
                    element: element,
                    label: label,
                    name: label,
                    position: markerPosition,
                    relativePosition: relativePosition,
                    type: "none",
                    isVisible: true
                }
            )
        }
        var markersHeader = `${markers.length} Assets Found`
        this.setState({ foundMarkerCount: markers.length, markersHeader: markersHeader, markers: markers })
    }

    markerLost(element) {

        console.log("marker lost: " + element)
        var index = this.state.markers.findIndex((m) => m.element === element)
        var markers = [...this.state.markers];
        if (index !== -1) {
            var marker = { ...markers[index] }
            // update position

            marker.isVisible = false;
            markers[index] = marker;
        }

        this.setState({ markers: markers })
    }

    getUpdatedMarkers(markers) {
        var markersCopy = [...markers];

        var anchorElement = document.querySelector("#anchor");
        var anchorPosition = anchorElement?.getAttribute("position");

        if (anchorPosition) {
            for (var i = 0; i < markersCopy.length; i++) {

                var markerElement = document.querySelector("#" + markersCopy[i].element);
                var markerPosition = markerElement?.getAttribute("position");

                var relativePosition = { x: markerPosition.x - anchorPosition.x, y: anchorPosition.y - markerPosition.y, z: 0 }

                var marker = { ...markersCopy[i] }
                marker.position = markerPosition;
                marker.relativePosition = relativePosition;
                markersCopy[i] = marker;
            }
        }
        return markersCopy;
    }

    getUpdatedFlight(markers) {
        return markers.map((m) => m.relativePosition);
    }

    handleItemOnClick(marker, index) {
        this.setState(
            {
                isMarkerPropertiesDialogOpen: true,
                markerPropertiesSelectedIndex: index,
                selectedMarkerType: marker.type,
                markerPropertiesName: marker.name,
                isMarkerTurnpoint: marker.isMarkerTurnpoint,
                markerPropertiesTurnPointNumber: marker.turnpointIndex,
            }
        );
    }

    closeMarkerPropertiesDialog() {
        this.setState({ isMarkerPropertiesDialogOpen: false, markerPropertiesSelectedIndex: -1 });
    }

    saveMarkerProperties() {
        var markersCopy = [...this.state.markers];

        var marker = { ...markersCopy[this.state.markerPropertiesSelectedIndex] }
        marker.name = this.state.markerPropertiesName;
        marker.type = this.state.selectedMarkerType;
        marker.isMarkerTurnpoint = this.state.isMarkerTurnpoint;
        marker.turnpointIndex = this.state.markerPropertiesTurnPointNumber;
        markersCopy[this.state.markerPropertiesSelectedIndex] = marker;

        this.setState({ markers: markersCopy, isMarkerPropertiesDialogOpen: false, markerPropertiesSelectedIndex: -1 });
    }

    componentDidMount() {
        this.timer = setInterval(() => {
            var i = this.state.loopIndex + 1;
            if (i > 1000000) {
                i = 0;
            }

            var markers = this.state.markers;
            var updatedMarkers = this.getUpdatedMarkers(markers);
            //Test code here that assumes all markers are flight points. Remove this when assigning marker types.

            if (updatedMarkers) {
                markers = updatedMarkers;
            }
            this.setState({ loopIndex: i, markers: markers });
        }, 100);

        document.getElementById("anchor").addEventListener("markerFound", (e) => { console.log("NG FOUND") });
        document.getElementById("marker1")?.addEventListener("markerFound", (e) => { this.addMarker("marker1", "M1") });
        document.getElementById("marker2")?.addEventListener("markerFound", (e) => { this.addMarker("marker2", "M2") });
        document.getElementById("marker3")?.addEventListener("markerFound", (e) => { this.addMarker("marker3", "M3") });
        document.getElementById("marker4")?.addEventListener("markerFound", (e) => { this.addMarker("marker4", "M4") });
        document.getElementById("marker5")?.addEventListener("markerFound", (e) => { this.addMarker("marker5", "M5") });
        document.getElementById("marker6")?.addEventListener("markerFound", (e) => { this.addMarker("marker6", "M6") });
        document.getElementById("marker7")?.addEventListener("markerFound", (e) => { this.addMarker("marker7", "M7") });
        document.getElementById("marker8")?.addEventListener("markerFound", (e) => { this.addMarker("marker8", "M8") });

        document.getElementById("marker1")?.addEventListener("markerLost", (e) => { this.markerLost("marker1") });
        document.getElementById("marker2")?.addEventListener("markerLost", (e) => { this.markerLost("marker2") });
        document.getElementById("marker3")?.addEventListener("markerLost", (e) => { this.markerLost("marker3") });
        document.getElementById("marker4")?.addEventListener("markerLost", (e) => { this.markerLost("marker4") });
        document.getElementById("marker5")?.addEventListener("markerLost", (e) => { this.markerLost("marker5") });
        document.getElementById("marker6")?.addEventListener("markerLost", (e) => { this.markerLost("marker6") });
        document.getElementById("marker7")?.addEventListener("markerLost", (e) => { this.markerLost("marker7") });
        document.getElementById("marker8")?.addEventListener("markerLost", (e) => { this.markerLost("marker8") });

        document.addEventListener('click', (e) => {
            if (e.target.className === 'a-canvas') {
                let bodyWidth = e.view.innerWidth / 2
                let bodyHeight = e.view.innerHeight / 2
                let y = e.y;
                let x = e.x;
                if (e.x + 350 > e.view.innerWidth) {
                    x = e.view.innerWidth - 300;
                }
                if (e.y + 233 > e.view.innerHeight) {
                    y = e.view.innerHeight - 233;
                }
                if (e.x < bodyWidth) {
                    if (e.y < bodyHeight) {
                        this.setState({
                            topLeft: !this.state.topLeft,
                            bottomLeft: false,
                            topRight: false,
                            bottomRight: false,
                            screenLocation: { left: x, top: y }
                        })
                    } else {
                        this.setState({
                            topLeft: false,
                            bottomLeft: !this.state.bottomLeft,
                            topRight: false,
                            bottomRight: false,
                            screenLocation: { left: x, top: y }
                        })
                    }
                } else {
                    if (e.y < bodyHeight) {
                        this.setState({
                            topLeft: false,
                            bottomLeft: false,
                            topRight: !this.state.topRight,
                            bottomRight: false,
                            screenLocation: { left: x, top: y }
                        })
                    } else {
                        this.setState({
                            topLeft: false,
                            bottomLeft: false,
                            topRight: false,
                            bottomRight: !this.state.bottomRight,
                            screenLocation: { left: x, top: y }
                        })
                    }
                }
            }
        })
    }

    componentWillUnmount() {
        if (this.timer) {
            window.clearInterval(this.timer);
        }
    }

    render() {
        return (
            <div>
                <Dialog open={this.state.isMarkerPropertiesDialogOpen} onClose={() => this.closeMarkerPropertiesDialog()} aria-labelledby="form-dialog-title">
                    <DialogTitle id="form-dialog-title">Marker Properties</DialogTitle>
                    <DialogContent>
                        <TextField
                            autoFocus
                            margin="dense"
                            label="Name"
                            defaultValue={this.state.markerPropertiesName}
                            onChange={(e) => this.setState({ markerPropertiesName: e.target.value })}
                            fullWidth
                        />
                        <Select value={this.state.selectedMarkerType} fullWidth onChange={(e) => this.setState({ selectedMarkerType: e.target.value })}>
                            <MenuItem value="none">None</MenuItem>
                            <MenuItem value="friendly">Friendly CVN</MenuItem>
                            <MenuItem value="enemy">Enemy Battle Group</MenuItem>
                        </Select>
                        <Switch
                            checked={this.state.isMarkerTurnpoint}
                            onChange={(e) => this.setState({ isMarkerTurnpoint: e.target.checked })}
                            name="Is Turnpoint"
                        />
                        <TextField
                            autoFocus
                            margin="dense"
                            label="Turn Point Number"
                            type="number"
                            defaultValue={this.state.markerPropertiesName}
                            onChange={(e) => this.setState({ markerPropertiesName: e.target.value })}
                            fullWidth
                        />
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={() => { this.closeMarkerPropertiesDialog() }} color="primary">
                            Cancel
                        </Button>
                        <Button onClick={() => { this.saveMarkerProperties() }} color="primary">
                            Save
                        </Button>
                    </DialogActions>
                </Dialog>
                <Grid container>
                    <Grid item xs={3}>
                        <Accordion>
                            <AccordionSummary
                                expandIcon={<ExpandMoreIcon />}
                            >
                                <Typography>{this.state.markersHeader}</Typography>
                            </AccordionSummary>
                            <AccordionDetails>
                                <List>
                                    {this.state.markers.map((marker, i) =>
                                    (<ListItem key={marker.element} button onClick={() => this.handleItemOnClick(marker, i)}>
                                        <ListItemText primary={marker.label} />
                                    </ListItem>)
                                    )}
                                </List>
                            </AccordionDetails>
                        </Accordion>
                    </Grid>
                    <Grid item xs={9}>
                        <ToggleButton value="orbit" aria-label="Orbit" selected={this.state.isOrbitActive} onChange={() => {
                            this.setState({ isOrbitActive: !this.state.isOrbitActive });
                        }} >
                            Route
                        </ToggleButton>
                        <ToggleButton value="borders" aria-label="Labels" selected={this.state.isBordersActive} onChange={() => {
                            this.setState({ isBordersActive: !this.state.isBordersActive });
                        }} >
                            Borders
                        </ToggleButton>
                        <ToggleButton value="weather" aria-label="Weather" selected={this.state.isWeatherActive} onChange={() => {
                            this.setState({ isWeatherActive: !this.state.isWeatherActive });
                        }} >
                            Weather
                        </ToggleButton>
                        <ToggleButton value="wind" aria-label="Wind" selected={this.state.isWindActive} onChange={() => {
                            this.setState({ isWindActive: !this.state.isWindActive });
                        }} >
                            Wind
                        </ToggleButton>
                        <ToggleButton value="terrain" aria-label="Terrain" selected={this.state.isTerrainActive} onChange={() => {
                            this.setState({ isTerrainActive: !this.state.isTerrainActive });
                        }} >
                            Terrain
                        </ToggleButton>
                        <ToggleButton value="labels" aria-label="Labels" selected={this.state.areLabelsActive} onChange={() => {
                            this.setState({ areLabelsActive: !this.state.areLabelsActive });
                        }} >
                            Labels
                        </ToggleButton>
                        <Button onClick={() => {
                        }}>
                            Export
                        </Button>
                    </Grid>
                </Grid>
                <DynamicLayer isOrbitActive={this.state.isOrbitActive} showLabels={this.state.areLabelsActive} loopIndex={this.state.loopIndex} markers={this.state.markers} />
                {this.state.isWeatherActive ? <WeatherLayer showLabels={this.state.areLabelsActive} index={this.state.loopIndex} /> : undefined}
                {this.state.isBordersActive ? <BordersLayer showLabels={this.state.areLabelsActive} /> : undefined}
                {this.state.isWindActive ? <WindLayer showLabels={this.state.areLabelsActive} /> : undefined}
                {this.state.isTerrainActive ? <Terrain parent="#terrain-layer" y={-5} /> : undefined}
                <Popup contentStyle={{ width: 300, position: "absolute", left: this.state.screenLocation.left, top: this.state.screenLocation.top }} open={this.state.bottomRight} popupClass={"popup-content"}>
                    <p>Communication Frequencies</p>
                    <p>Aviation Distress : 121.500 MHz</p>
                    <p>Air Traffic Control 118.000  121.950 MHz</p>
                    <p>Ground : 121.3 MHz</p>
                </Popup>
                <Popup contentStyle={{ width: 300, position: "absolute", left: this.state.screenLocation.left, top: this.state.screenLocation.top }} open={this.state.topRight} popupClass={"popup-content"}>
                    <p>HOSTILE</p>
                    <p>Threat Level : Midnight</p>
                    <p>Range : 50NM</p>
                </Popup>
                <Popup contentStyle={{ width: 350, position: "absolute", left: this.state.screenLocation.left, top: this.state.screenLocation.top }} open={this.state.bottomLeft} popupClass={"popup-content"}>
                    <p>NOTAMS</p>
                    <p>AERODROME PRECISION APPROACH RADAR OUT OF SERVICE RWY 32L. 08 JUL 16:18 2021
                        UNTIL 27 AUG 23:59 2021. CREATED: 08 JUL 16:18 2021</p>
                    <p>RWY 05R/23L ALL LIGHTS OUT OF SERVICE. 08 JUL 13:54 2021 UNTIL 16 JUL 23:59 2021.
                        CREATED: 08 JUL 13:54 2021</p>
                </Popup>
                <Popup contentStyle={{ width: 300, position: "absolute", left: this.state.screenLocation.left, top: this.state.screenLocation.top }} open={this.state.topLeft} popupClass={"popup-content"}>
                    <p>Weather</p>
                    <p>Wind : 40 Knots NE</p>
                    <p>Visibility : 5NM</p>
                    <p>*Severe Thunderstorm Warning*</p>
                </Popup>
            </div>
        );
    }
}