import React from "react";
import { Triangle } from './triangle'
import { Circle } from "./circle";

export class Threat extends React.Component {
    render() {
        return <div>
            <Triangle parent={this.props.parent} color={this.props.iconColor} size={this.props.size ?? .1} x={this.props.x} y={this.props.y} z={this.props.z + .1}/>
            <Circle parent={this.props.parent} color={this.props.circleColor} radius={this.props.radius} x={this.props.x} y={this.props.y} z={this.props.cZ - .1} transparent={true} opacity={0.125} />
        </div>;
    }
}
